import Vue from 'vue'
import Router from 'vue-router'
import home from '../pages/Home'
import messages from '../pages/Messages'
import aboutme from '../pages/Aboutme'
import filterroom from '../pages/Filterroom'
import detailpages from '../pages/detailPage'
import lorr from '../pages/loginOrRegister'
import VerifyOrder from  '../pages/verifyroom'
import orderStatus from '../pages/OrderStatus'
import mpagedetail from '../pages/messageDetail'
import scan from '../pages/scanQRcode'
import verifyID from '../pages/verifyIdentify'
import userReg from '../pages/uRegister'
import empty from  '../pages/emptyState'
import picker from '../pages/DatePicker'
import reservationDetail from '../pages/Detailedcontent'
import mMsg from '../pages/meetingMsg'

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      meta: {
        title: '首页',
        navShow: true,
        keepAlive: false
      },
      component: home
    },
    {
      path: '/home',
      name: 'home',
      meta: {
        title: '首页',
        navShow: true,
        keepAlive: false
      },
      component: home
    },
    {
      path: '/message',
      name: 'messages',
      meta: {
        title: '消息列表',
        navShow: true,
        keepAlive: false
      },
      component: messages
    },
    {
      path: '/person', // 用户个人页面
      name: 'aboutme',
      meta: {
        title: '我的信息',
        navShow: true,
        keepAlive: false
      },
      component: aboutme
    },
    {
      path: '/filterRoom', // 会议室选择列表页
      name: 'filterroom',
      meta: {
        title: '筛选详情页',
        navShow: false,
        keepAlive: true
      },
      component: filterroom,
      /*children: [
        {
          path:'/filterRoom/detail',
          component: detailpages
        }
      ]*/
    },
    {
      path: '/detail',
      name: 'detailpages',
      meta: {
        title: '会议室详细',
        navShow: false,
        keepAlive: false
      },
      component: detailpages
    },
    {
      path: '/loginRegister',
      name: 'lorr',
      meta: {
        title: '用户登录',
        navShow: false,
        keepAlive: false
      },
      component: lorr
    },
    {
      path: '/userForReg',
      name: 'uReg',
      meta: {
        title: '用户注册',
        navShow: false,
        keepAlive: false
      },
      component: userReg
    },
    {
      path: '/verifypage',
      name: 'VerifyOrder',
      meta: {
        title: '提交预订',
        navShow: false,
        keepAlive: false
      },
      component: VerifyOrder
    },
    {
      path: '/orderList',
      name: 'orderStatus',
      meta: {
        title: '订单列表',
        navShow: false,
        keepAlive: false
      },
      component: orderStatus
    },
    {
      path: '/messagePageDetail',
      name: 'orderMessageStatus',
      meta: {
        title: '消息详情页',
        navShow: false,
        keepAlive: false
      },
      component: mpagedetail
    },
    {
      path: '/scanQRcode',
      name: 'scanCode',
      meta: {
        title: '扫一扫',
        navShow: false,
        keepAlive: false
      },
      component: scan
    },
    {
      path: '/verifyIdentity',
      name: 'submitPhoto',
      meta: {
        title: '身份认证',
        navShow: false,
        keepAlive: false
      },
      component: verifyID
    },
    {
      path: '/reservation',
      name: 'reservation',
      meta: {
        title: '预订详情页',
        navShow: false,
        keepAlive: false
      },
      component: reservationDetail
    },
    {
      path: '/emptyState',
      name: 'empty',
      meta: {
        title: '空白',
        navShow: false,
        keepAlive: false
      },
      component: empty
    },
    {
      path: '/meetingMsg',
      name: 'mMsg',
      meta: {
        title: '会议消息提醒',
        navShow: false,
        keepAlive: false
      },
      component: mMsg
    },
    {
      path: '/dateSelected',
      name: 'datePicker',
      meta: {
        title: '时间选择',
        navShow: false,
        keepAlive: false
      },
      component: picker
    },
    {
      path: '*',    // 当页面不存在时，重定向到首页
      redirect: '/'
    }
  ]
})



