<template>
    <div @click="goForDC">
      <md-card>
        <md-card-header>
          <md-card-header-text>
            <div class="md-title">{{ roomName }}</div>
            <div class="md-subhead">{{ roomPlace }}</div>
          </md-card-header-text>

          <md-card-media>
            <img :src="ImgSrc" alt="People">
          </md-card-media>
        </md-card-header>

      </md-card>
    </div>
</template>

<script>
    export default {
        name: "homebriefItem",
        props: {
          roomName: {
            type: String,
            default: '硅谷'
          },
          roomPlace: {
            type: String,
            default: '地球村的三号公寓'
          },
          ImgSrc: {
            type: String,
            default: '../assets/rhapsody.png'
          }
        },
      methods:  {
        goForDC: function() {
          this.$router.push('/reservation');
        }
      }
    }
</script>

<style lang="scss" scoped>
  .md-card-header .md-card-header-text {
    flex: 0 1 auto;
  }
  .md-card-header .md-card-media {
    width: 100px;
    height: 80px;
    margin-left: 16px;
    flex: 0 1 auto;


  }
  .md-card-media img {
    width: 100%;
    height: 100%;
  }

</style>



