<template>
  <div v-if="isLogin"  >
    <div class="separator" >
      <md-avatar class="md-large">
        <img src="https://placeimg.com/40/40/people/5" alt="People">
      </md-avatar>
      <div class="self-nickname">
          <div class="self-nickname-name">gbhj434</div>
          <div>工号： 16401190223</div>
      </div>
    </div>

    <md-list class="md-dense " >
      <md-list-item   @click="turntoOrderStatus">
        <md-icon class="md-accent">event_note</md-icon>
        <span class="md-list-item-text">我的订单</span>
        <md-button class="md-icon-button md-list-action">
          <md-icon>chevron_right</md-icon>
        </md-button>
      </md-list-item>
      <md-list-item @click="goVerifyIdenty">
        <md-icon class="md-primary">how_to_reg</md-icon>
        <span class="md-list-item-text">身份认证</span>
        <md-button class="md-icon-button md-list-action" >
          <md-icon>chevron_right</md-icon>
        </md-button>
      </md-list-item>
      <md-list-item @click="clearCookie">
        <md-icon class="">home</md-icon>
        <span class="md-list-item-text" >退出登录</span>
        <md-button class="md-icon-button md-list-action" >
          <md-icon>chevron_right</md-icon>
        </md-button>
      </md-list-item>
    </md-list>
  </div>

  <div class="flex-center"  v-else>
    <div class="md-empty-state ">
      <md-icon class="md-size-5x">sentiment_very_dissatisfied</md-icon>
      <md-content>
        <h3>未登录账号</h3>
      </md-content>
      <md-button class="md-primary md-raised" @click="TurntoLogin">点击登录</md-button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Aboutme",
      data: function() {
        return {
            isLogin:  this.$store.state.isLogin
        }
      },
      methods: {
        /**
         * 退出登录时调用的方法
         */
        clearCookie: function() {
          if ( this.$store.state.wsObj != null ) {
            this.$store.state.wsObj.onclose = function () {
              console.log("连接已关闭...");
            };
          }

          this.$store.commit('exitLogin');
          this.$router.replace('/emptyState');
        },
        testCookie: function() {
          this.$http.get(this.root_url.api_header + '/user/user').then(res => {
            console.log(res);
          })
        },
        TurntoLogin: function () {
          this.$router.push('/loginRegister')
        },
        turntoOrderStatus: function() {
          this.$router.push('/orderList')
        },
        goVerifyIdenty: function () {
          this.$router.push('/verifyIdentity');
        }

      }
    }
</script>

<style lang="scss" scoped>
  .flex-center {
    padding-top: 10rem;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
  }
  .separator {
    padding: 30px 0;
    padding-left: 16px;
    .md-avatar.md-large {
      min-width: 96px;
      min-height: 96px;
      border-radius: 96px;
      font-size: 32px;
      display: inline-block;

    }
    .self-nickname {
      display: inline-flex;
      height: 96px;
      padding-left: 16px;
      flex-wrap: wrap;
      align-content: center;
      align-items: center;

      .self-nickname-name {
        display: block;
        width: 100%;
        line-height: 48px;
        font-family: Roboto,Tahoma,Verdana,"Lucida Console","Lucida Bright",Helvetica,"Trebuchet MS",monospace,Arial,cursive,新宋体, "Microsoft YaHei UI",sans-serif;
      }

    }
  }
</style>



