        data: function () {
          return {
            MessagePageTitle: '预约提醒',
            reservation: []
            /*perfect: true,

            roomName: '黑木崖',
            roomTime: '2019/3/10 14:00-17:00',
            roomPosition: '邵逸夫楼7幢4层409',
            reciveTime: '18:08'*/
            /*reservation: [
              /!*{
                roomName: '',
                roomTime: '',
                roomPosition: '',
                reciveTime: ''
              }*!/
            ]*/

          }
        },
        mounted () {

            var time;   //将开会时间转换出来
            var moment;
            var endTime;
            var msgList = this.$store.state.myRsvList;
            console.log(msgList);
            if (msgList != null) {
              for (var i = 0; i < msgList.length; i++) {
                console.log(msgList[i]);
                time = new Date(msgList[i].reservation.startdatetime);
                endTime = new Date(msgList[i].reservation.enddatetime);
                var timeArr = time.toLocaleString().split(' ');
                console.log(timeArr[0]);
                var perfectTime = timeArr[0] + ' '+ this.getLocalTime(msgList[i].reservation.startdatetime, 2) + '-' + this.getLocalTime(msgList[i].reservation.enddatetime, 2);
               /* if (timeArr[1].substring(2, timeArr[1].length - 3).length == 4) {
                  perfectTime = timeArr[0] + ' 0'+ timeArr[1].substring(2, timeArr[1].length - 3);
                }
                timeArr = endTime.toLocaleString().split(' ');
                if (timeArr[1].substring(2, timeArr[1].length - 3).length == 4) {
                  perfectTime = perfectTime + '-0' + timeArr[1].substring(2, timeArr[1].length - 3);
                }else {
                  perfectTime = perfectTime + '-' + timeArr[1].substring(2, timeArr[1].length - 3);
                }
*/
                console.log(perfectTime);
                /* var momentTime = new Date();
                 var fullMonth = momentTime.getMonth() + 1;
                 var fullDay = momentTime.getDate();
                 var fullHour = momentTime.getHours();
                 var fullMinute = momentTime.getMinutes();
                 var fullSecond = momentTime.getSeconds();
                 if (fullMonth < 10) fullMonth = '0' + fullMonth;
                 if (fullDay < 10)  fullDay += '0';
                 if (fullHour < 10 ) fullHour = '0' + fullHour;
                 if (fullMinute < 10 ) fullMinute = '0' + fullMinute;
                 if (fullSecond < 10 ) fullSecond = '0' + fullSecond;
                 moment = momentTime.getFullYear() + '-' + fullMonth + '-' + fullDay ;
                 moment += ' ' + fullHour + ':' + fullMinute + ':' + fullSecond;
                 console.log(moment);
                  console.log(this.formatDate(moment));*/
                var msgItem = {
                  roomName: msgList[i].room.name,
                  roomTime: perfectTime,
                  roomPosition: msgList[i].room.place,
                  reciveTime: this.formatDate(this.getLocalTime(msgList[i].reservation.createtime))
                };
                // console.log(JSON.parse());
                this.reservation.push(msgItem);
              }

            }

            this.$store.commit('setOperateMsg', -this.$store.state.OperateMsg);

        },
        beforeDestroy: function() {
          this.$store.commit('setnewPosts');
        },
        methods: {
          Goback: function () {
            this.$router.push('/message')
          },
          getLocalTime: function (timeStamp, needType) {
            var moment;
            var momentTime = new Date(timeStamp);
            var fullMonth = momentTime.getMonth() + 1;
            var fullDay = momentTime.getDate();
            var fullHour = momentTime.getHours();
            var fullMinute = momentTime.getMinutes();
            var fullSecond = momentTime.getSeconds();
            if (fullMonth < 10) fullMonth = '0' + fullMonth;
            if (fullDay < 10)  fullDay += '0';
            if (fullHour < 10 ) fullHour = '0' + fullHour;
            if (fullMinute < 10 ) fullMinute = '0' + fullMinute;
            if (fullSecond < 10 ) fullSecond = '0' + fullSecond;
            moment = momentTime.getFullYear() + '-' + fullMonth + '-' + fullDay ;
            moment += ' ' + fullHour + ':' + fullMinute + ':' + fullSecond;
            if (needType == 2) {
              return  fullHour + ':' + fullMinute;
            }
            return moment;
          },

          formatDate:  function(last_time) {
            //var last_str = ("2019-03-03 10:16:00").replace(/-/g, "/");//一般得到的时间的格式都是：yyyy-MM-dd hh24:mi:ss，所以我就用了这个做例子，是/的格式，就不用replace了。
            var  last_str = last_time.replace(/-/g, "/");
            var date = last_str.substr(0, 10);
            var time = last_str.substr(11, 5);
            var day = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"]
            var tempdate = new Date(date)
            var last_date = new Date(last_str);//将字符串转化为时间
            var now = new Date();
            var num = (now - tempdate) / (1000 * 3600 * 24);//求出两个时间的时间差，这个是天数
            var days = parseInt(num);//转化为整天（小于零的话剧不用转了）
            //console.log(days)
            var res ;
            if (days == 0) {
              res = "今天 " + time;
            } else if (days == 1) {
              res = "昨天 " + time;
            } else if (days > 1 && days < 7) {
              res  = "昨天 " + time;
            }
            else {
              res = last_str.substr(0,16);
            }
            return res;
          }

        }
    }
</script>

