<template>
  <div id="app">
    <BottomNavigation v-show="$route.meta.navShow"></BottomNavigation>
    <div class="content-panel">
      <!--<router-view/>-->
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive" ></router-view>
      </keep-alive>
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </div>
  </div>
</template>

<script>
import BottomNavigation from "./components/BottomNavigation";
export default {
  name: 'App',
  components: {BottomNavigation},
  mounted: function () {
      let _this = this;

    this.$store.commit('setwsObj', null);
    console.log('将 WebSocket 对象置空有没有走 ');
      if (this.$store.state.isLogin == true) {    //状态为已登录才走开始 ws 连接
        console.log("登录后开始WebSocket连接");
        const  ws = new WebSocket('ws://test.icms.work/websocket/' + this.$store.state.LoginId);
        this.$store.commit('setwsObj', ws);
        ws.onopen = function () {
          console.log('ws onopen');
          ws.send('from client: hello');
        };
        ws.onmessage = function (e) {
          let msg = JSON.parse(e.data);
          console.log('ws onmessage');
          console.log('from server: ' + JSON.parse(e.data));
          // console.log(e.data.reservation);
          if (e.data != null ) {
            // if (e.data.reservation)
            /*console.log(e.data);
            _this.$store.commit('setOperateMsg', 1);*/
            // _this.$store.commit('setisNewOrder');
            if (_this.$store.state.LoginId != msg.reservation.originator )  {
              console.log(msg.reservation.originator);
              _this.$store.commit('setremindMsg', 1);
              _this.$store.commit('setisNewOrder');
            }else {
              _this.$store.commit('setisNewOrder');
              _this.$store.commit('setOperateMsg', 1);
            }
            _this.$store.commit('setisNewOrder');
            console.log( _this.$store.state.OperateMsg);
            _this.$store.commit('setnewPosts');
            _this.$root.eventHub.$emit('eventName', '有数据变动了');
          }
          if (_this.$store.state.myRsvList.length != 0) {
            _this.$store.commit('setisHereOder');
            _this.$store.commit('setisNewOrder');
          }
/*
          if (_this.$store.state.myRsvList == null )  {
            _this.$store.commit('setmyRsvList',JSON.parse(e.data));
            _this.$store.commit('setisNewOrder');
          }
*/
          _this.$store.commit('updatemyRsvList',JSON.parse(e.data));
          _this.$store.commit('setisNewOrder');
          console.log('这里的 新消息变量究竟有没有更新');

        };
      }
  },
  updated() {
    var _this = this;
    if (this.$store.state.wsObj != null) {
      /*this.$store.state.wsObj.onopen = function () {
        console.log('ws onopen');
        this.$store.state.wsObj.send('from client: hello');
      };*/
      this.$store.state.wsObj.onmessage = function (e) {
        let msg = JSON.parse(e.data);
        console.log('ws onmessage');
        console.log('在应用启动时监听数据传入');
        console.log('from server: ' + JSON.parse(e.data));
        // console.log(e.data.reservation);

        _this.$store.commit('setisNewOrder');
        if (msg != null || msg != '') {
          if (_this.$store.state.LoginId != msg.reservation.originator )  {
            _this.$store.commit('setisNewOrder');
            console.log(msg.reservation.originator);
            _this.$store.commit('setremindMsg', 1);
          }else {
            _this.$store.commit('setOperateMsg', 1);
            _this.$store.commit('setisNewOrder');
          }
          console.log( _this.$store.state.OperateMsg);
          _this.$store.commit('setnewPosts');
          _this.$root.eventHub.$emit('eventName', '有数据变动了');
        }
        if (_this.$store.state.myRsvList.length != 0) {
          _this.$store.commit('setisHereOder');
          _this.$store.commit('setisNewOrder');
        }
/*
        if (_this.$store.state.myRsvList == null )  {
          _this.$store.commit('setmyRsvList',JSON.parse(e.data));
          _this.$store.commit('setisNewOrder');
        }
*/
        _this.$store.commit('updatemyRsvList',JSON.parse(e.data));
        _this.$store.commit('setisNewOrder');
        console.log('这里的 新消息变量究竟有没有更新');
      };
    }else {
      if (this.$store.state.isLogin == true ) {
        console.log('I Meeting 的 websocket 连了没有？');

        const  ws = new WebSocket('ws://test.icms.work/websocket/' + this.$store.state.LoginId);
        this.$store.commit('setwsObj', ws);
        ws.onopen = function () {
          console.log('ws onopen');
          ws.send('from client: hello');
        };
        ws.onmessage = function (e) {
          let msg = JSON.parse(e.data);
          console.log('ws onmessage');
          console.log('from server: ' + JSON.parse(e.data));
          console.log(msg.reservation);
          if (msg != null ||msg !=  '') {
            /*console.log(e.data);
            _this.$store.commit('setOperateMsg', 1);*/
            // _this.$store.commit('setisNewOrder');
            if (_this.$store.state.LoginId != msg.reservation.originator )  {
              console.log(msg.reservation.originator);
              _this.$store.commit('setisNewOrder');
              console.log( _this.$store.state.isNewOrder);
              _this.$store.commit('setremindMsg', 1);
            }else {
              _this.$store.commit('setisNewOrder');
              _this.$store.commit('setOperateMsg', 1);
            }
            // _this.$store.commit('setisNewOrder');
            console.log( _this.$store.state.OperateMsg);
            _this.$store.commit('setnewPosts');
            // _this.$root.eventHub.$emit('eventName', '有数据变动了');
          }
          if (_this.$store.state.myRsvList.length != 0) {
            _this.$store.commit('setisHereOder');
          }
          _this.$store.commit('updatemyRsvList',JSON.parse(e.data));
          _this.$store.commit('setisNewOrder');
          console.log('这里的 新消息变量究竟有没有更新');

        };

      }

    }

  }

}
</script>

<style lang="scss" scoped>
  *{
    margin: 0;
    padding: 0;
  }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  width: 100%;
  height: 100%;
  max-width: 420px;
  max-height: 750px;
  .content-panel {
    width: 100%;
    overflow-x: hidden;
  }
}
</style>



