<template>
  <div class="self-list-card">
    <p class="self--order-title">{{ roomName }}</p>
    <div style="display: flex;flex-wrap: nowrap">
      <div class="self-order-image"><img :src="imgSrc" alt=""></div>
      <div class="self-order-content">
        <b>{{ orderDate }} {{ orderTime }}</b>
        <p>地点：<strong>{{ orderPosition }}</strong></p>
      </div>
    </div>
    <div class="self-status">当前状态：<span :class="orderType">{{ orderStatus }}</span></div>

  </div>
</template>

<script>
    export default {
        name: "orderListItem",
        props: {
          imgSrc: {
            type: String,
            default: '../assets/applicationImage/list-one.jpg'
          },
            roomName: {
              type: String,
              default: '铜雀台会议厅'
            },
          orderDate: {
              type: String,
              default: '2019/3/08'
          },
          orderTime: {
              type: String,
              default: '14:00-17:00'
          },
          orderPosition: {
              type: String,
              default: '风华图书馆G区负一层',
          },
            orderStatus: {
              type: String,
              default: '已结束'
            },
            orderType: {
              type: String,
              default: ''
            }
        }
    }
</script>

<style lang="scss" scoped>
  .self-list-card {
    width: -webkit-calc(100%);
    margin:.4rem  0;
    padding-left: .7rem;
    height: 6.5rem;
    background-color: #fff;
    .self--order-title {
      margin: 0;
      height: 1.5rem;
      line-height: 1.5rem;
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: .9rem;
      font-weight: normal;
    }
    %order-list-box {
      height: 3rem;
      margin:0;
      margin-top: 0.3rem;
      padding: 0;
      display: inline-block;
    }
    .self-order-image,.self-order-content{
      @extend %order-list-box;
    }
    .self-order-image {
      width: 4.2rem ;
    }
    .self-order-content {
      width:-webkit-calc(100% - (5.2rem));
      padding-left: 1rem;
      strong {
        font-weight: normal;
      }
      b, p, strong {
        margin: 0;
        padding: 0;
      }
      overflow-y: hidden;
    }
    .self-status {
      margin:0;
      padding: 0;
      height: 1.7rem ;
      width: 100%;
      padding-right: 1rem;
      font-size: .9rem;
      text-align: right;
      position: relative;
      span {
        color: #757575;
        font-family: "Adobe 黑体 Std R",sans-serif;
      }
      .processing {
        color: #E63946;
      }
    }
  }

</style>



