<template>
  <div class="messageDetailPanel" @click="goForDC">
    <div class="self-notification-item">
      <div class="self-pushTimeStamp">{{ reciveTime }}</div>
      <div class="self-rectangle md-elevation-2" >

        <div class="notifyType">{{ msgTitle }}</div>
        <div class="self-text-key self-roomName">会议室    <span>{{ roomName }}</span></div>
        <div class="self-text-key self-roomTime">预约时间    <span>{{ roomTime }}</span></div>
        <div class="self-text-key self-roomposition">位置<span>{{ roomPosition }}</span></div>
        <div v-show="perfect" >
          查看会议详情
        </div>
      </div>
    </div>
  </div>

</template>

<script>
    export default {
        name: "bookNotification",
      props: {
        msgTitle: {
          type: String,
          default: '预约消息提醒'
        },
        reciveTime: {
          type: String,
          default: '18:08'
        },
        perfect: {
          type: Boolean,
          default: true
        },
        /*MessagePageTitle: {
          type: String,
          default: '开会提醒'
        },*/
        roomName: {
          type: String,
          default: '黑木崖'
        },
        roomTime: {
          type: String,
          default: '2019/3/10 14:00-17:00'
        },
        roomPosition: {
          type: String,
          default: '邵逸夫楼7幢4层409'
        }
      },
      methods: {
        goForDC: function() {
          this.$router.push('/reservation');
        }
      }
    }
</script>

<style lang="scss" scoped>
  %self-divider {
    right: 0;
    left: 0;
    /*background-color: rgba(0,0,0,0.12);*/
    background-color: var(--md-theme-default-divider-on-background, rgba(0,0,0,0.12));
    height: 1px;
    position: absolute;
    bottom: -.5rem;
    z-index: 99;
    content: " ";
  }
  %common-reset {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  body {
    background-color: #ededed;

    .self-notification-item {
      padding: 0 1rem;
      @extend %common-reset;
      .self-pushTimeStamp {
        width: 100%;
        display: flex;
        height: 2.5rem;
        font-size: .6rem;
        justify-content: center;
        align-items: center;
      }
      .self-rectangle {
        box-shadow: 0 1px 3px rgba(0,0,0,.2), 0 1px 1px rgba(0,0,0,.14), 0 2px 1px -1px rgba(0,0,0,.12);
        width: 100%;
        height: 11rem;
        margin-bottom: 2px;
        border-radius: .2rem;
        background-color: #fff;
        padding: .6rem .6rem  0;

        .notifyType {
          font-size: .8rem;
          font-family: sans-serif;
          color: #000;
          letter-spacing: 1.1px;
          font-weight: normal;
          position: relative;
        }
        %subTitle {
          font-size: .8rem;
          color:#9e9faa;
          opacity: .9;
        }
        .self-text-key {
          @extend %subTitle;
        }
        %subtitle-two {
          display: flex;
          flex-wrap: nowrap;
          align-items: flex-end;
        }
        .self-roomName {
          height: 3rem;
          @extend %subtitle-two;
          span {
            display: inline-flex;
            /*width: 65%;*/
            font-size: 1.2rem;
            line-height: 2.5rem;
            vertical-align: bottom;
            justify-content: center;
            align-items: flex-end;
            /*align-self: center;*/
            color: #000;
            flex: 1;
          }
        }
        .self-roomTime {
          height: 1.8rem;
          @extend %subtitle-two;
          span {
            color: #000;
            letter-spacing: 0.7px;
            /*width: 65%;*/
            font-size: 1rem;
            display: inline-flex;
            justify-content: center;
            flex: 1;
          }
        }
        .self-roomposition {
          position: relative;
          height: 1.8rem;
          margin-bottom: 1rem;
          @extend %subtitle-two;

          &:after {
            @extend %self-divider
          }
          span {
            color: #000;
            font-size: 1rem;
            display: inline-flex;
            justify-content: center;
            flex: 1;
          }
        }
      }
    }
  }

</style>



