// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueMaterial from 'vue-material'    /* 导入 谷歌 Material 设计*/
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import "vue-material-design-icons/styles.css"
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'       //能把json格式的直接转成data所需的格式
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"
import root_url from '../src/api/api'
import $ from 'jquery'
import Vueg from'vueg';

// import lottie from 'node_modules/vue-lottie/src/lottie.vue';
import * as animationData from '../src/assets/lottie/1798-check-animation'
// Vue.component('lottie', lottie);
// import 'vueg/css/transition-min.css';

const Voptions= {
  duration:'0.3',//转场动画时长，默认为0.3，单位秒
  firstEntryDisable:false,//值为true时禁用首次进入应用时的渐现动画，默认为false
  firstEntryDuration:'.6',//首次进入应用时的渐现动画时长，默认为.6
  forwardAnim:'fadeInRight',//前进动画，默认为fadeInRight
  backAnim:'fadeInLeft',//后退动画，默认为fedeInLeft
  sameDepthDisable:false,//url深度相同时禁用动画，默认为false
  tabs: [],//默认为[]，name对应路由的name,以实现类似app中点击tab页面水平转场效果，如tab[1]到tab[0]，会使用backAnim动画，tab[1]到tab[2]，会使用forwardAnim动画
  tabsDisable:false,//值为true时，tabs间的转场没有动画，默认为false
};//disable:false//禁用转场动画，默认为false，嵌套路由默认为true


// import store from './store/store'

Vue.prototype.root_url = root_url;

axios.defaults.timeout = 5000;  //响应时间
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';           //配置请求头

axios.get('/static/config.json').then((res) => {
  // 基础地址
  Vue.prototype.BASE_URL = res.data.BASE_URL;
  // console.log(res);
});

axios.defaults.withCredentials=true;

Vue.use(Vuex);
Vue.use( Vueg, router, Voptions);



const store = new Vuex.Store({
  state: {
    wsObj: null,        //离线消息
    myRsvList: [],    //消息列表
    LoginId: null,      //登录获取到的Id
    userNickName: '',       //用户名称
    userJobNum: '',         //用户工号

    isLogin: false,     //是否登录状态
    RoomMessageContainer: { },
    currentItem: { },     //已废弃，原来是用来保存筛选获取到的数据的
    userToken: null,      //有歧义，错误的让用户以为token
    isHereOder: false,    //是否有订单
    isNewOrder: 0,      //这里是否有消息
    newPosts: 0,     //消息的总数，用于底部导航栏的显示
    AppMsg: 0,        //应用消息
    OperateMsg: 0,    //操作消息
    remindMsg: 0,       //提醒消息
    isVerify: false
  },
  mutations: {
    changeLoginState (state) {
      state.isLogin = true
    },
    exitLogin (state) {
      state.isLogin = false;
    },
    getCorporationdata(state, params) {
      state.RoomMessageContainer = params;
    },
    setCurrentBrowse(state, params = null) {
      state.currentItem = params;
    },
    setUsertoken(state, params) {
      state.userToken = params;
    },
    setnewPosts(state) {
      state.newPosts = state.AppMsg + state.OperateMsg + state.remindMsg;
    },
    setAppMsg(state, count) {
      state.AppMsg = count;
    },
    setOperateMsg(state, count) {
      state.OperateMsg += count;
    },
    setremindMsg(state, count) {
      state.remindMsg += count;
    },

    setLoginId(state, id) {
      state.LoginId = id;
    },
    setmyRsvList(state, param) {
      state.myRsvList = param;
    },

    updatemyRsvList(state, param) {
      state.myRsvList.push(param);
    },

    setwsObj(state, param) {
      state.wsObj = param;
    },

    clearOperateMsg(state) {
      state.OperateMsg = 0;
    },
    setisHereOder(state) {
      state.isHereOder = true;
    },
    setVerify(state) {
      state.isVerify = true;
    },
      setisNewOrder(state) {
      state.isNewOrder ++ ;
    }

  },
  plugins: [createPersistedState()]
});



Vue.prototype.$http=axios;    //全局注册，使用方法为:this.$axios
/*axios.defaults.baseURL = 'http://test.icms.work/api/v1';
axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';*/
Vue.prototype.qs = qs;           //全局注册，使用方法为:this.qs
Vue.use(VueMaterial);
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>',
  data: {
    eventHub: new Vue()
  }
});



