<script>
  export default {
    data: function () {
      return {
        first: false,
        detectionResult: false,
        loading: false,
        isNoImg: true,
        name:'',
        department: '',
        Jobnumber: '',    //工号
        sex: null,    //性别
        age: '',
        file: null,
        param: null,
        config: null
      }
    },
    methods: {
      onConfirm: function() {
        this.$router.push('/home');
      },
      Goback: function () {
        this.$router.go(-1)
      },
      clearSelected: function() {
        /*var obj = document.getElementById('virtualButton') ;
        obj.select();
        document.selection.clear();*/
        // $obj = document.getElementById('virtualButton') ;
        $("#virtualButton").val('');
        let $img = document.getElementById('img');
        $img.setAttribute("src",'');
        this.isNoImg = true;

      },
      update(e){
        let $img = document.getElementById('img');
        let  reader = new FileReader();
        let file = e.target.files[0];
        reader.readAsDataURL(file);
        let _this = this;
        reader.onload = function(e){
          $img.setAttribute("src", e.target.result);
          _this.isNoImg = false;
          console.log(e.target.result)
        };
        let maxSize = 2 * 1024 * 1024;   //200KB     限定图片文件大小
        console.log(file.size);
        if (file.size > maxSize) {
          alert('图片容量大小不合格');
          return;
        }
        let picUserabled = new FormData();
        picUserabled.append('file', file, file.name);
        this.$http.post(this.root_url.api_header + '/user/testfaceimg', picUserabled, {
          headers:{'Content-Type':'multipart/form-data'}
        } ).then(res => {
          if(res.data.code == 200) {
            this.detectionResult = true;
          }
        });

        let param = new FormData(); //创建form对象
        param.append('file',file,file.name);//通过append向form对象添加数据
        // param.append('chunk','0');//添加form表单中其他数据
        param.append('department',this.department);
        param.append('common_id',this.Jobnumber);
        param.append('name',this.name);
        param.append('sex',this.sex);
        param.append('age',this.age);

        this.param =param;
        console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
        let configheader = {
          headers:{'Content-Type':'multipart/form-data'}
        };  //添加请求头
        this.config = configheader;

      },
      submitImg:function() {
        var _this = this;
        this.loading = true;
        setTimeout(function () {
          _this.loading = false;
          _this.first = true;
        },2000);
        this.$http.post(this.root_url.api_header + '/user/verify',this.param,this.config)
          .then(response=>{
            console.log(response.data);
            if ( response.data.code == 200) {
              this.$store.commit('setVerify');
            }
          });
      },

      uploadIMg: function(e,c, d){
        "use strict";
        var $c = document.querySelector(c),
          $d = document.querySelector(d),
          reader = new FileReader();
        // this.file = $c.files[0];
        console.log(this.file);
          reader.readAsDataURL(this.file);
        reader.onload = function(e){
          $d.setAttribute("src", e.target.result);
          console.log(e.target.result)
        };

        var img = new Image();
          img.src = e.target.result;
        var canvas = document.getElementById('picPreview'),
          ctx = canvas.getContext('2d');
        canvas.width = img.width;
        console.log(img.width);
        canvas.height = img.height;

        //利用canvas进行绘图

        //将原来图片的质量压缩到原先的0.2倍。
        var data = canvas.toDataURL('image/jpeg', 0.2); //data url的形式
      },

      uploadFile: function () {
        this.$http.post(this.root_url.api_header + '/user/testfaceimg',{
          file: this.file
        })
      }
    }
  }

</script>
