<template>
  <div class="scan">
    <div id="bcid">
      <div style="height:40%"></div>
      <p class="tip">...载入中...</p>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>

  //点手机虚拟返回键
  document.addEventListener("plusready", function () {
    // 注册返回按键事件
    plus.key.addEventListener('backbutton', function () {
      // 事件处理
      scan.close();//关闭条码识别控件
      window.history.back();
    }, false);
  });

  let scan = null;

  export default {
    data() {
      return {
        codeUrl: '',
      }
    },
    mounted() {
      // setTimeout(() => {
      this.startScan();
      // }, 0)

    },
    methods: {
      goBack: function() {
        this.$router.go(-1);
      },
      //创建扫描控件
      startRecognize() {
        let that = this;
        if (!window.plus) return;
        that.isShow = false;
        // 创建条码扫描识别控件
        scan = new plus.barcode.Barcode('bcid',
          [plus.barcode.QR, plus.barcode.EAN8, plus.barcode.EAN13],
          {frameColor: "#00FF00", scanbarColor: "#00FF00"});
        // 条码识别成功
        scan.onmarked = onmarked;

        function onmarked(type, result, file) {
          switch (type) {
            case plus.barcode.QR:
              type = 'QR';
              break;
            case plus.barcode.EAN13:
              type = 'EAN13';
              break;
            case plus.barcode.EAN8:
              type = 'EAN8';
              break;
            default:
              type = '其它' + type;
              break;
          }

          result = result.replace(/\n/g, '');
          that.codeUrl = result;//扫描后返回值
          //alert(result);
          that.jump(result);
          if (that.codeUrl) {
            that.isShow = true
          }
        }

      },
      //开始扫描
      startScan() {
        if (!window.plus) return;
        this.startRecognize()//创建控件
        scan.start();
      },
      jump(result) {
        scan.cancel();
        scan.close();
        //扫码识别结果处理
        if (result != null || result != '' ) {
          // console.log(this.$route.params.obj);
          let url = result;
          console.log(result);
          console.log('扫描结果拿到了');
          //let url = 'http://test.icms.work/QRCode?room_id=1';
          let getRoomId = url.substring( (url.indexOf('=')+1) );
          console.log(getRoomId);
          console.log("拿到了房间号");
          console.log(this.root_url.api_header + '/user/getroomdetial');
          this.$router.push({
            name: 'detailpages' ,
            params:{
              date: new Date(),
              scan_id: getRoomId
            }})
          /*this.$http.post(this.root_url.api_header + '/user/getroomdetial',{
            date: new Date(),
            roomid: getRoomId
          }).then(res => {
            console.log(res);
            console.log('开始跳转');
            if (res.data.code == 200)
            this.$router.push({
              name: 'detailpages' ,
              params:{
                obj: res
              }});
            // console.log(res);
          });*/


        }
        /*this.$router.push({ name: 'home', params: {
          obj: result
          }});*/
      },
      beforeDestroy() {
        if (scan) {
          //关闭扫描控件
          scan.cancel();
          scan.close();
        }
      },
    }
  }
</script>
<style lang="scss" scoped>
  .scan {
    height: 100vh;
    width: 100%;
    position: relative;
    z-index: 999999;
    #bcid {
      width: 100%;
      position: absolute;
      z-index: 99999999999;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      text-align: center;
      color: #fff;
      background: #fff;
    }

  }
</style>



