<template>
    <div class="bottomNavigation">
      <!-- 底部导航栏组件 -->
      <md-bottom-bar class="md-accent">
        <md-bottom-bar-item md-label="首页" md-icon="home" @click="handleRouter"></md-bottom-bar-item>
        <md-bottom-bar-item md-label="消息" md-icon="message"  :md-template-data="{ badge: $store.state.newPosts }" @click="handleMessage"></md-bottom-bar-item>
        <md-bottom-bar-item md-label="我" md-icon="person" @click="handlePerson"></md-bottom-bar-item>
      </md-bottom-bar>
      <div class="self-badge" :class="{ hasMsg: judge }">{{ MsgNum }}</div>
    </div>
</template>

<script>
    export default {
        name: "BottomNavigation",
      data: function() {
          return {
            judge: false,
            MsgNum: 0,
          }
      },
      mounted: function() {
        this.$root.eventHub.$on('eventName', (data)=>{
          // 处理数据
          this.$store.commit('setnewPosts');
          // this.$store.commit('setisHereOder');
        });
        this.$store.commit('setnewPosts');
        if ( !this.$store.state.isLogin) {
            this.$router.replace('/loginRegister');
          }
          // let MsgCount = this.$store.state.AppMsg + this.$store.state.OperateMsg + this.$store.state.remindMsg;
          let MsgCount = this.$store.state.newPosts;
           this.judge = (MsgCount == 0);

           if (!this.judge ){
             this.MsgNum = MsgCount;
           }
      },
      updated: function() {
        if ( !this.$store.state.isLogin) {
          this.$router.replace('/loginRegister');
        }

        if( this.$store.state.myRsvList.length != 0) {
          this.$store.commit('setisHereOder');
        }

        this.$store.commit('setnewPosts');
        if ( !this.$store.state.isLogin) {
          this.$router.replace('/loginRegister');
        }
        // let MsgCount = this.$store.state.AppMsg + this.$store.state.OperateMsg + this.$store.state.remindMsg;
        let MsgCount = this.$store.state.newPosts;
        this.judge = (MsgCount == 0);

        if (!this.judge ){
          this.MsgNum = MsgCount;
        }
      },
      beforeDestroy() {
        let MsgCount = this.$store.state.newPosts;
        this.judge = (MsgCount == 0);

        if (!this.judge ){
          this.MsgNum = MsgCount;
        }
      },
      methods: {
        handleRouter () {
          this.$router.push('/home')
        },
        handleMessage () {
          this.$router.push('/message')
        },
        handlePerson () {
          this.$router.push('/person')
        }
      }
    }
</script>

<style lang="scss" scoped>
.bottomNavigation {
  width: 100%;
  height: auto;
  position: fixed;
  display: flex;
  bottom: 0;
  left: 0;
  z-index: 9999;
  align-items: flex-end;
  border-top: 1px solid rgba(#000, 0.06);
}
  .self-badge {

    width: 19px;
    height: 19px;
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    /*position: absolute;*/
    /*top: 2px;*/
    /*right: 2px;*/
    background: red;
    border-radius: 100%;
    color: #fff;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: -.05em;
    font-family: Roboto Mono,monospace;

    position: fixed;
    transition: .3s cubic-bezier(.4,0,.2,1);
    /*display: flex;*/
    bottom: 34px;
    left: 52%;
    z-index: 99999999999999;
    /*align-items: center;*/
    /*justify-content: center;*/

    /*font-size: 10px;*/
    /*font-style: normal;*/
    /*width: 22px;*/
    /*height: 22px;*/
    /*border-radius: 50%; */
    /*color: #fff;*/
    pointer-events: none;
    /*z-index: 6;*/
  }
  .hasMsg {
    display: none;
  }

</style>



