<template>
  <!--<div @click="changeToDetail">
    <md-card class="md-primary"   md-theme="md-theme-default" >
      <md-card-header>
        <md-card-header-text>
          <div class="md-title">{{ roomName }}</div>
          <div class="md-subhead">当前状态:{{ statusText }}</div>
        </md-card-header-text>
        <md-card-media>
          <img :src="imgSrc" alt="Avatar">
        </md-card-media>
      </md-card-header>
    </md-card>
  </div>-->

  <div @click="changeToDetail">
    <md-card class="md-primary"   md-theme="md-theme-default" >
      <div class="self-header-box">
        <div class="self-img-box">
          <img :src="imgSrc" alt="">
        </div>
        <div class="self-room-tag">
          <div class="card-title">{{roomName}}（{{position}}）</div>
          <div class="card-subtitle"><span class="tag">{{ min_p }}-{{ max_p }}人</span></div>
          <div class="car-tag">
            <template v-for="item in devices">
              <span class="devices">{{ item }}</span>
            </template>
             <!--<span class="devices">话筒</span>  -->
            <span class="type">{{ roomType }}</span>
          </div>
        </div>
      </div>

      <!--<md-card-header>-->
        <!--<md-card-header-text>-->
          <!--<div class="md-title">{{ roomName }}</div>-->
          <!--<div class="md-subhead">当前状态:{{ statusText }}</div>-->
        <!--</md-card-header-text>-->
        <!--<md-card-media>-->
          <!--<img :src="imgSrc" alt="Avatar">-->
        <!--</md-card-media>-->
      <!--</md-card-header>-->
    </md-card>
  </div>

</template>

<script>
    export default {
        name: "filterItem",
        props:  {
          position: {
            type: String,
            default: '三楼309'
          },
          devices: {
            type: Array,
            default: ()=> ['投影仪','话筒','打印机']
          },
          roomName: {
            type: String,
            default: '财务室'
          },
          personAccommodate: {
            type: Number,
            default: 20
          },
          min_p: {
            type: Number,
            default: 4
          },
          max_p: {
            type: Number,
            default: 10
          },
          status: {
            type: Boolean,
            default: false
          },
          roomType: {
            type: String,
            default: '报告厅'
          },
          /*devices: {
            type: Array,
            default: []
          },*/
          imgSrc: {
            default:'static/list-one.jpg'
          },
          statusText: {
            type: String,
            default: '空闲'
          }
      },
      /*computed:{
        roomStatus() {
          if (this.status) {
            return '使用中';
          }else {
            return '空闲'
          }
        }
      },*/
      methods: {
        changeToDetail () {
          // this.$router.push({path: '/detail', query: {id: false}})
          // this.$router.push('filterRoom/detail');
          this.$emit('reclick');
        }
      }
    }
</script>

<style  lang="scss" scoped>
  .md-card {
    margin: 4px;
    vertical-align: top;
  }
  .md-card-header:first-child>.md-card-header-text>.md-title:first-child, .md-card-header:first-child>.md-title:first-child {
    margin-top: 0;
    font-size: 20px;
  }
  .md-card-media {
    width: 120px;
    height: 84px;
    flex:0 1 auto;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .self-header-box {
    padding: 9px;
    display: flex;
    align-content: center;
    align-items: center;
  }
  .self-img-box {
    width: 120px;
    max-height: 84px;
    display: inline-block;
  }
  .self-room-tag {
    display: inline-block;
    flex: 1;
    height: 84px;
    padding-left: 1rem;
    .card-title,.card-subtitle, .car-tag{
      height: 28px;
      line-height: 28px;
      /*align-self: center;*/
      /*vertical-align: center;*/
      /*display: inline-block;*/
    }
    .card-title {
      font-weight: 600;
      font-family:Roboto,Tahoma,Verdana,"Lucida Console","Lucida Bright",Helvetica,"Trebuchet MS",monospace,Arial,cursive,新宋体, "Microsoft YaHei UI",sans-serif;
      opacity: 1;
    }
    .card-subtitle {
      display: flex;
      align-content: center;
      align-items: center;
        .tag {
          height: 18px;
          line-height: 14px;
          display: inline-flex;
          vertical-align: center;
          border: 1px solid #2196F3;
          color: #2196F3;
          padding:1px  2px;
          border-radius: 4px;
          font-size: 12px;
          /*justify-content: center;
          align-content: center;*/
        }
    }
    .car-tag {
      font-size: 12px;
      color: #212121;
      opacity: .7;
      .devices{
        margin-left: 2px;
        margin-right: 2px;
        display: inline-block;
        &:first-child {
          margin-left: 0;
          margin-right: 2px;
        }
      }
      .type {
        display: inline-block;
        float: right;
        font-size: 12px;
        font-family:Verdana,monospace,sans-serif ;
        opacity: .6;
      }
    }
  }

</style>



