<template>
    <div>
      <md-toolbar :md-elevation="1">
        <div class="md-toolbar-row">
          <div class="md-toolbar-section-start">
            <md-button class="md-icon-button" @click="Goback">
              <md-icon>keyboard_backspace</md-icon>
            </md-button>
          </div>

          <div class="md-toolbar-row">
            <h3 class="md-title">会议列表</h3>
          </div>
        </div>
<!--
        <span class="md-title"></span>
-->
      </md-toolbar>
      <md-tabs class="md-transparent" md-alignment="fixed"  md-sync-route >
        <md-tab id="tab-processing" md-label="预订">
          <!--<order-item room-name="聚贤庄" order-status="未开始" order-type="processing" order-date="2019/3/10"
          order-time="11:00-12:00" order-position="中官西路365号六层102"></order-item>-->
          <template v-for="item in futureOL">
            <orderItem :room-name="item.roomName"  :img-src="item.ImgSrc" :order-date="item.roomDate"  :order-type="processing" :order-time="item.roomTime"
                       :order-position="item.roomPosition" :order-status="futureStatus"></orderItem>
          </template>
        </md-tab>
        <md-tab id="tab-cancel" md-label="进行中">
          <template v-for="item in alreadyOL">
            <orderItem :room-name="item.roomName"  :img-src="item.ImgSrc"  :order-date="item.roomDate" :order-time="item.roomTime"
                       :order-position="item.roomPosition" :order-status="ingStatus"></orderItem>
          </template>
        </md-tab>
        <md-tab id="tab-finish" md-label="已完成" >
          <template v-for="item in orderList">
            <orderItem :room-name="item.roomName"  :img-src="item.ImgSrc" :order-date="item.roomDate" :order-time="item.roomTime"
                       :order-position="item.roomPosition" :order-status="PassStatus"></orderItem>
          </template>
<!--
          <orderItem></orderItem>
-->
        </md-tab>

      </md-tabs>
    </div>
</template>

<script>
  import orderItem from "../components/orderListItem"
    export default {
        name: "OrderStatus",
      components: {orderItem},
      data: function() {
        return {
          futureStatus: '未开始',
          ingStatus: '进行中',
          PassStatus: '已完成',
          futureOL: [],
          orderList: [
             /*{
                roomName: '',
                roomDate: '',
                roomTime: '',
                roomPosition: '',
                orderStatus:''
            }*/
          ],
          alreadyOL: []

        }
      },
      mounted: function() {
        var time;   //将开会时间转换出来
        var endTime;
        var status;
        var day;
        var moment = new Date().getTime();
        var msgList = this.$store.state.myRsvList;
        console.log(msgList);
        if (msgList != null) {
          for (var i = 0; i < msgList.length; i++) {
            time = new Date(msgList[i].reservation.startdatetime);
            endTime = new Date(msgList[i].reservation.enddatetime);
            /*console.log(time.toLocaleString());
            console.log(endTime.toLocaleString());*/
            /*console.log(msgList[i].reservation.startdatetime);
            console.log(moment);
            console.log(msgList[i].reservation.enddatetime);*/
            //状态判断
            if (moment < msgList[i].reservation.startdatetime) status = "未开始";
            if (msgList[i].reservation.startdatetime < moment < msgList[i].reservation.enddatetime ) status = '进行中';
            if ( moment > msgList[i].reservation.enddatetime ) status = '已完成';
            var timeArr = time.toLocaleString().split(' ');
            day = timeArr[0];
            // var perfectTime = timeArr[1].substring(2, timeArr[1].length - 3);
            // console.log(perfectTime);
            //处理上下午时间差的问题
            var perfectTime;
            /*if (timeArr[1].substring(2, timeArr[1].length - 3).length == 4 ) {
              perfectTime = '0'+ timeArr[1].substring(2, timeArr[1].length - 3);
            }
            if( timeArr[1].substring(0,2) == '下午') {

            }
            timeArr = endTime.toLocaleString().split(' ');
            if (timeArr[1].substring(2, timeArr[1].length - 3).length == 4) {
              perfectTime =perfectTime +  '-0' + timeArr[1].substring(2, timeArr[1].length - 3);
            }else {
              perfectTime =perfectTime +  '-' + timeArr[1].substring(2, timeArr[1].length - 3);
            }*/

            perfectTime = this.getLocalTime(msgList[i].reservation.startdatetime) + '-' + this.getLocalTime(msgList[i].reservation.enddatetime);
            // console.log(perfectTime);
            /* var momentTime = new Date();
             var fullMonth = momentTime.getMonth() + 1;
             var fullDay = momentTime.getDate();
             var fullHour = momentTime.getHours();
             var fullMinute = momentTime.getMinutes();
             var fullSecond = momentTime.getSeconds();
             if (fullMonth < 10) fullMonth = '0' + fullMonth;
             if (fullDay < 10)  fullDay += '0';
             if (fullHour < 10 ) fullHour = '0' + fullHour;
             if (fullMinute < 10 ) fullMinute = '0' + fullMinute;
             if (fullSecond < 10 ) fullSecond = '0' + fullSecond;
             moment = momentTime.getFullYear() + '-' + fullMonth + '-' + fullDay ;
             moment += ' ' + fullHour + ':' + fullMinute + ':' + fullSecond;
             console.log(moment);
              console.log(this.formatDate(moment));*/
            let tag = msgList[i].room.imagepath.substring(1,msgList[i].room.imagepath.length-1).split(',')[0];
            var imgsrc = 'http://test.icms.work/static/roomimage/'+ msgList[i].room.room_id + '/' + tag;
            var msgItem = {
              roomName: msgList[i].room.name,
              roomDate: day ,
              roomTime: perfectTime,
              roomPosition: msgList[i].room.place,
              orderStatus: status,
              ImgSrc: imgsrc
            };
            // console.log(JSON.parse());
            if (moment < msgList[i].reservation.startdatetime){
              this.futureOL.push(msgItem);
            }else if ( moment > msgList[i].reservation.enddatetime )  {
              this.orderList.push(msgItem);
            } else {
              this.alreadyOL.push(msgItem);
            }
          }

        }

      },
      methods:{
        getLocalTime: function (timeStamp) {
          var moment;
          var momentTime = new Date(timeStamp);
          var fullMonth = momentTime.getMonth() + 1;
          var fullDay = momentTime.getDate();
          var fullHour = momentTime.getHours();
          var fullMinute = momentTime.getMinutes();
          var fullSecond = momentTime.getSeconds();
          if (fullMonth < 10) fullMonth = '0' + fullMonth;
          if (fullDay < 10)  fullDay += '0';
          if (fullHour < 10 ) fullHour = '0' + fullHour;
          if (fullMinute < 10 ) fullMinute = '0' + fullMinute;
          if (fullSecond < 10 ) fullSecond = '0' + fullSecond;
         /* moment = momentTime.getFullYear() + '-' + fullMonth + '-' + fullDay ;
          moment += ' ' + fullHour + ':' + fullMinute + ':' + fullSecond;*/
          moment = fullHour + ':' + fullMinute;
          return moment;
        },
        Goback: function () {
          this.$router.go(-1)
        }
      }
    }
</script>

<style lang="scss" scoped>
  body {
    background-color: #fafafa;
  }

</style>



