<template>
  <div>
    <div class="centered-container">
      <md-content class="md-elevation-0 self-full-screen">
        <md-button class="md-icon-button self-location" @click="backLogin">
          <md-icon>keyboard_backspace</md-icon>
        </md-button>

        <h5 class="md-title self-page-tag">注册</h5>
        <div class="form">
          <md-field>
            <label>手机号码</label>
            <md-input v-model="userRphone"  maxlength="11"></md-input>
            <span class="md-error">There is an error</span>
          </md-field>
          <div class="_msg-code">
            <md-field md-clearable>
               <label>短信验证码</label>
                  <md-input id="msmCode" v-model="verifyCode"  :disabled="messageCode" required maxlength="4" autofocus="autofocus"></md-input>
            </md-field>
          </div>
          <div class="_msg_send">
            <a href="#" class="send_code" @click="sendsmsCode" :class="{ 'unclickabled': isSendCode }">{{ sendTips }}</a>
          </div>
          <md-field>
            <label>设置密码</label>
            <md-input v-model="password" type="password" maxlength="16" ></md-input>
            <span class="md-error">两次密码不一致</span>
          </md-field>

          <md-field :md-toggle-password="false">
            <label>确认密码</label>
            <md-input v-model="Vpassword" type="password"  maxlength="16"></md-input>
            <span class="md-error">两次密码不一致</span>
          </md-field>
        </div>

        <div class="actions md-layout md-alignment-center-space-between">
          <md-button class="md-raised md-primary uregister" @click="authReg">提交注册</md-button>
        </div>

        <div class="loading-overlay" v-if="loading">
          <md-progress-spinner md-mode="indeterminate" :md-stroke="2"></md-progress-spinner>
        </div>

        <div class="title">
          <img src="../assets/imeeting.png">
          <!--<div class="md-title">加 入 开 会 啦</div>-->
        </div>
      </md-content>
    </div>
    <md-dialog-alert
      :md-active.sync="first"
      md-content="短信验证码已发送，请注意接收!"
      md-confirm-text="确定" />

  </div>
</template>

<script>
    export default {
        name: "uRegister",
        data:function () {
          return {
            isSendCode: false,
            sendTips: '发送短信验证码',
            setInputFocus: '',
            first: false,   //消息提示框
            messageCode: true,      //短信验证框不可用
            loading: false,
            userRphone: '',
            verifyCode: '',     //短信验证码
            password:'',
            Vpassword:'',   //确认密码
          }
        },
      methods: {
        sendsmsCode: function() {

          this.$http.get(this.root_url.api_header + '/user/getsmscode?phonenum=' + this.userRphone).then(res => {
            this.messageCode = false;
            // this.verifyCode = res.data.data.SMSCode;
            console.log(this.verifyCode);
            this.first = true;
            setTimeout(function () {
              document.getElementById('msmCode').focus();
            },1500);
          });
          this.isSendCode = true;
          var timer1;
          var i = 60;
          var _this = this;
          function test(){
            i--;
            _this.sendTips = '重新发送' +  '(' + i + ')';
            if (i<=0){
              _this.sendTips = '发送短信验证码';
              _this.isSendCode = false;
              clearInterval(timer1);
              i = 60;
            }
          }
          timer1 = setInterval(test,1000);
          /*for (var i = 60; i > 0; i--) {

            setTimeout(function () {
              this.sendTips = '(' + i + ')';
            }, 1000);
          }
          this.isSendCode = false;*/

        },
        backLogin:function () {
          this.$router.go(-1);
        },
        authReg: function () {
          this.loading = true;
          let code;
          this.$http.post(this.root_url.api_header + '/user/sign',{
            phonenum:this.userRphone,
            password: this.Vpassword,
            smscode: this.verifyCode
          },{
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          }).then(res=>{
            let data = res.data;
            console.log(data);
            code = data.code;
            setTimeout(() => {
              this.loading = false
            }, 1000);
            this.$router.push('/loginRegister');

          }).catch(error=>{
            console.log(error);
            this.loading = false
          });
        }
      }
    }
</script>

<style  lang="scss" scoped>
  .md-layout.md-alignment-center-space-between {
    justify-content: flex-end;
  }
  /*.uregister {
    display: inline-block;
    float: right;
  }*/
  ._msg-code {
    display: inline-block;
    width: 50%;
  }
  ._msg_send {
    display: inline-block;
    width:48%;
    text-align: right;

  }
  .send_code {
    display: inline-block;
    padding: 8px ;
    /*font-size: 12px;*/
    border-radius: 4px;
    border: 1px solid #448aff;
  }
  .centered-container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    width: 100%;
    .self-full-screen {
      width: 100%;
      padding:0 40px 40px ;
      max-width: 420px!important;
      height: 100vh!important;
      /*margin-bottom: 20vh;*/
      padding-top: 70px!important;

      .self-location {
        position: absolute;
        left: 10px;
        top: 10px;
      }



    }
  }

  .title {
    text-align: center;
    margin-top: 30px;
    img {
      margin-bottom: 16px;
      max-width: 80px;
    }
  }
.self-page-tag {
  margin: 0 ;
}
.unclickabled {
  cursor: pointer;
  color: rgba(0,0,0,.29)!important;
  border-color: rgba(0,0,0,.29) !important;
}
  .loading-overlay {
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
  }

</style>



