          md-layout="box">
          <label>Search...</label>
        </md-autocomplete>

        <div class="md-toolbar-section-end">

          <md-button class="md-icon-button">
            <md-icon>more_vert</md-icon>
          </md-button>
        </div>
      </div>
    </md-toolbar>
    <!--  ---------------------搜索栏和工具栏结束 -------------------- -->
    <div class="self-control-overflow">
      <md-menu md-size="medium" :mdCloseOnSelect="true" md-align-trigger >
        <md-button md-menu-trigger class="self-button-icon self-stretch" :class="{'md-primary': Blueone}">人数</md-button>
        <md-menu-content>
          <template v-for="(itemOne, index) in Quantity">
            <md-menu-item v-if="itemOne.personNum[1] == 101" @click="QuantityClick(index)" :class="{'md-primary': itemOne.conditionOneSelected}">{{ itemOne.personNum[0] }}人以上</md-menu-item>

            <md-menu-item v-else @click="QuantityClick(index)" :class="{'md-primary': itemOne.conditionOneSelected}">{{ itemOne.personNum[0] }}-{{ itemOne.personNum[1] }}人</md-menu-item>

          </template>

        </md-menu-content>
      </md-menu>
      <md-menu md-size="medium" :mdCloseOnSelect="true" md-align-trigger>
        <md-button md-menu-trigger class="self-button-icon self-stretch" :class="{'md-primary': Bluetwo}">时间</md-button>

        <md-menu-content>
          <template v-for="(two, index) in DateTwo">
            <md-menu-item  @click="twoConditionClick(index)" :class="{'md-primary': two.conditionTwoSelected}">{{ two.dayItem[0] }}月{{ two.dayItem[1] }}日</md-menu-item>
          </template>
        </md-menu-content>
      </md-menu>

      <md-menu md-size="medium" :mdCloseOnSelect="true" md-align-trigger>
        <md-button md-menu-trigger class="self-button-icon self-stretch" :class="{'md-primary': Bluethree}">类型</md-button>

        <md-menu-content>
          <template v-for="(three, index) in frRoomType">
            <md-menu-item  @click="threeConditionClick(index)" :class="{'md-primary': three.condition3Selected}">{{ three.typeName }}</md-menu-item>
          </template>
     <!--     <md-menu-item>My Item 2</md-menu-item>
          <md-menu-item>My Item 3</md-menu-item>-->
        </md-menu-content>
      </md-menu>
      <md-menu md-size="medium" :mdCloseOnSelect="true" :md-close-on-click="true" md-align-trigger>
        <md-button md-menu-trigger class="self-button-icon self-stretch" :class="{'md-primary': Bluefour}">设备</md-button>

        <md-menu-content >
          <template v-for="(item, index) in devicesData">
            <md-menu-item :class="{'md-primary':item.isitemSelected }" @click="itemMutiSelected(index)"  >{{ item.personNum }}</md-menu-item>
          </template>
        </md-menu-content>
      </md-menu>

    </div>
    <template v-for="(item, index) in roomFilterList">
      <filter-item :room-name="item.name" :status-text="item.statusText"  :position="item.place"
      :min_p="item.min_p" :max_p="item.max_p"   :devices="item.equipment"  :img-src="item.imgSrc" @reclick="goToDetail(index)" ></filter-item>
    </template>
  </div>
</template>

<script>
  import filterItem from '../components/filterItem'
  import axios from 'axios'
    export default {
        name: "Filterroom",
      data: () => ({
        CurrentBrowse: {},  //当前浏览
        /**
         * 四个筛选参数值
         */
        frUserConditions: {
          minP: null,   //最少人数
          maxP: null,   //最大人数
          filterday: new Date(),    //查询日期
          roomType: null,       //会议室类型
          devices: []         //设备类型
        },
        /**
         *  四个筛选条件
         */
        Blueone: false,
        Bluetwo: false,
        Bluethree: false,
        Bluefour: false,
        /**
         *  人数数据对象
         */
        Quantity: [
          {
            personNum:[4,10],
            conditionOneSelected: false,
          },
          {
            personNum: [15,30],
            conditionOneSelected: false,
          },
          {
            personNum: [30,50],
            conditionOneSelected: false,
          },
          {
            personNum: [50,100],
            conditionOneSelected: false,
          },
          {
            personNum: [100,101],
            conditionOneSelected: false,
          }
        ],
        /**
         *  日期数据对象
         */
        DateTwo: [
          {
            dayItem: [
              new Date().getMonth()+1,
              new Date().getDate()
            ],
            conditionTwoSelected: false,
          },
          {
            dayItem: [
              (new Date().getMonth()+1) ,
              (new Date().getDate()+1 )
            ],
            conditionTwoSelected: false,
          },
          {
            dayItem: [
              (new Date().getMonth()+1),
              (new Date().getDate()+2 )
            ],
            conditionTwoSelected: false,
          },
          {
            dayItem: [
              (new Date().getMonth()+1),
              (new Date().getDate()+3 )
            ],
            conditionTwoSelected: false,
          },
          {
            dayItem: [
              (new Date().getMonth()+1),
              (new Date().getDate()+4 )
            ],
            conditionTwoSelected: false,
          },
          {
            dayItem: [
              (new Date().getMonth()+1) ,
              (new Date().getDate()+5 )
            ],
            conditionTwoSelected: false,
          },
          {
            dayItem: [
              (new Date().getMonth()+1),
              (new Date().getDate()+6 )
            ],
            conditionTwoSelected: false,
          }
        ],
        /**
         *  会议室类型
         */
        frRoomType: [
          {
            typeName: '剧院式',
            condition3Selected: false
          },
          {
            typeName: '课桌式',
            condition3Selected: false
          },
          {
            typeName: 'U型',
            condition3Selected: false
          },
          {
            typeName: '回型（双U型）',
            condition3Selected: false
          },
          {
            typeName: '椭圆型',
            condition3Selected: false
          },
          {
            typeName: '长方型',
            condition3Selected: false
          },
          {
            typeName: '圆桌式',
            condition3Selected: false
          },
          {
            typeName: '鸡尾酒会式',
            condition3Selected: false
          },
          {
            typeName: '宴会式',
            condition3Selected: false
          },
          {
            typeName: 'T型台',
            condition3Selected: false
          }
        ],
        /**
         *  设备数据对象
         */
        devicesData: [
          {
            personNum: '打印机',
            isitemSelected: false
          },
          {
            personNum: '投影仪',
            isitemSelected: false
          },
          {
            personNum: '音响',
            isitemSelected: false
          },
          {
            personNum: '无线麦克',
            isitemSelected: false
          }
        ],
        selectedEmployee: null,
        /**
         *  搜索栏数据对象
         */
        // employees: [
        //   '桐花台',
        //   '滴翠亭',
        //   '药王谷',
        //   '聚贤庄',
        //   '达摩院',
        //   '黑木崖',
        //   '一寸金',
        //   '春晴',
        //   '水漫声',
        //   '梦玉人引',
        //   '双色连',
        //   '四季花',
        //   '勇士',
        //   '黄蜂'
        // ],
        employees: [],
        searchMap: [
          {
            employees: [],
            employeeId: []
          }
        ],
        /**
         *  列表数据总来源
         */
        roomFilterList: [
          /*{
            name: "鸟巢",
            statusText:'空闲',
            status: false,
            imgSrc: 'static/appImg/list-18.jpg',
            place: "首都" ,
            room_id: 2,
            min_p: 4,
            max_p: 10,
            equipment: ['投影仪','话筒','打印机'],
            timeStamp: [],
            timeDetail: [],
            combination: {
              timeStamp: false,
              timeDetail: ''
            }
          }*/
        ]
      }),
      components: {
        filterItem
      },
      /*beforeRouteLeave(to, from, next) {
        // 设置本路由的 meta
        to.meta.keepAlive = false;  // 离开 A 时，让 A 缓存，即不刷新
        next();
      },*/
      mounted: function() {

        let _this = this;
        axios.post(this.root_url.api_header + '/user/getRoomList',{
            "device": null,
            "num_limit": {
              "max": null,
              "min": null
            },
            "querydate": new Date(),
            "type": ""
          },
          {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          }).then( function (res) {
          _this.initialData(res.data.data)
        });

      },
      beforeRouteEnter(to, from, next) {
        next();
        /*axios.post(this.root_url.api_header + '/user/getRoomList',{
          "device": null,
          "num_limit": {
            "max": null,
            "min": null
          },
          "querydate": new Date(),
          "type": ""
        },
          {
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          }).then( function (res) {

            next(vm => {
              vm.initialData(res.data.data)
            });
        });*/
      },
      beforeRouteLeave(to, from, next) {
        this.$store.commit('setCurrentBrowse', this.CurrentBrowse);
        this.$store.commit('setUsertoken',new Date());
        console.log('成功了吗');
        console.log(this.CurrentBrowse);
        next(vm => {
          // console.log(vm.$store.state.currentItem.userToken);
        });
      },
      methods: {
        TurnSpecRoom: function(val) {
          console.log("搜索结果");
          console.log(val);
          console.log(this.searchMap.employees[0]);
          console.log(this.searchMap);
          let room_id ;
          let max_p;
          for (var s = 0; s < this.searchMap.length; s++) {
             if (this.searchMap.employees[s] == val) {
               room_id = this.searchMap.employeeId[s];
               max_p  =  this.searchMap.max_p;
               console.log(this.searchMap[s].employeeId[s]);
             }
          }
          /*this.searchMap.forEach(function (value, index, array) {
            if (array[index].employees == val) {
              room_id = array[index].employeeId;
              console.log(array[index].employeeId);
            }
          });*/
          console.log(room_id);
          this.$router.push({
            name:"detailpages",
            params:{
             /* name:this.roomFilterList[index].name,
              imgSrc:this.roomFilterList[index].imgSrc,*/
              date: this.frUserConditions.filterday,
              scan_id: room_id,
              max_p: max_p
            }
          });
          // this.goToDetail(1);
        },
        SearchResult: function() {
          /*let obj= {
            name:   this.selectedEmployee
          };*/
          // console.log( );
          /*if (this.selectedEmployee = '' || null) {
            return;
          }*/
          this.employees.splice(0, this.employees.length);  //改变时清空数组
          axios.post(this.root_url.api_header + '/user/getRoomByName?name='+ this.selectedEmployee,
            {
              headers: {
                'Content-Type': 'application/json;charset=UTF-8'
              }
            })
            .then(res =>{
            console.log('会议室搜索结果数据');
            console.log(res.data);
            let employeeId = [];
            let employees = res.data.data;
            if (employees.length == 0) {
              return;
            }
            let _this = this;
              employees.forEach(function (value, index, array) {
                _this.employees.push(array[index].name);
                employeeId.push(array[index].room_id);
              });
              this.searchMap.employees = this.employees;
              this.searchMap.employeeId = employeeId;
              this.searchMap.max_p =  array[index].max_people;
              console.log(this.employees);
              console.log(employeeId);
              console.log(this.searchMap.employees[0]);
            });
          // console.log(this.employees[0].name);
        },
        sendFilter: function(minP=null, maxP=null, filterday=new Date(), roomType=null, devices=null ) {
          axios.post( this.root_url.api_header + '/user/getRoomList',{
            "device": devices,
            "num_limit": {
              "max": maxP,
              "min": minP
            },
            "querydate": filterday,
            "type": roomType
          }).then(res =>{
            this.initialData(res.data.data);
          })
        },
        initialData: function (data) {
          console.log('转入成功');
          console.log(data);
          this.roomFilterList = [];
          // console.info(data[0].timeStamp[0]);
          if (data.length <= this.roomFilterList.length){
            if(data.length == 0) {
              this.roomFilterList.splice(0, this.roomFilterList.length - data.length);
            }
            this.roomFilterList.splice(data.length-1, this.roomFilterList.length - data.length);
          }else if (data.length > this.roomFilterList.length) {
            for (let j = this.roomFilterList.length; j < data.length; j++) {
              let str = data[j].room.equipment;
              let strs = new Array();
              str = str.substring(1,str.length-1);
              // console.log(str.split(','));
              /*for (var s = 0; i < str.length; s++ ) {
                strs.push(str[i].substring(1,str[i].length-1));
              }*/
              let newItem = {
                name: data[j].room.name,
                statusText:'空闲',
                status: false,
                imgSrc: 'static/appImg/list-18.jpg',
                place: data[j].room.place ,
                room_id: data[j].room.room_id,
                min_p: data[j].room.min_people,
                max_p: data[j].room.max_people,
                equipment: str.split(','),
                timeStamp: [],
                timeDetail: [],
                combination: {
                    timeStamp: false,
                    timeDetail: ''
                  }
              };
              data[j].timeStamp.forEach(function (value, index, array) {
                newItem.timeStamp.push(array[index]);
                let timeStage;
                timeStage = (index + 8) + ':00-' + (index + 9) +':00';
                if (index+ 8 < 10) {
                  timeStage = '0' + (index + 8) + ':00-' + (index + 9) +':00';
                  if (index+ 9 < 10) {
                    timeStage = '0' + (index + 8) + ':00-' + '0' + (index + 9) +':00';
                  }
                }
                newItem.timeDetail.push(timeStage);
                newItem.combination.timeStamp = array[index];
                newItem.combination.timeDetail = timeStage;
              });
              /*console.log('看看时间');
              console.log(newItem.timeStamp);*/
              this.roomFilterList.push(newItem);
            }

          }
          this.roomFilterList.forEach(function (value, index, array) {
            array[index].name = data[index].room.name;
            let tag = data[index].room.imagepath.substring(1,data[index].room.imagepath.length-1).split(',')[0];
            array[index].imgSrc = 'http://test.icms.work/static/roomimage/'+ data[index].room.room_id + '/' + tag;
            /*console.log(13);
            console.log(array[index].imgSrc);*/
          });

        },
        QuantityClick(index) {
          if (this.Quantity[index].conditionOneSelected == false) {
            this.Quantity.forEach(function (value, index, array) {
              array[index].conditionOneSelected = false;
            });
            this.Quantity[index].conditionOneSelected = true;
            this.Blueone = true;
            this.frUserConditions.minP = this.Quantity[index].personNum[0];
            this.frUserConditions.maxP = this.Quantity[index].personNum[1];
            this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
              this.frUserConditions.roomType, this.frUserConditions.devices);
          } else {
            this.Quantity[index].conditionOneSelected = false;
            this.Blueone = false;
            this.frUserConditions.minP = null;
            this.frUserConditions.maxP = null;
            this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
              this.frUserConditions.roomType, this.frUserConditions.devices);
          }
        },
        twoConditionClick(index) {
          if (this.DateTwo[index].conditionTwoSelected == false) {
            this.DateTwo.forEach(function (value, index, array) {
              array[index].conditionTwoSelected = false;
            });
            this.DateTwo[index].conditionTwoSelected = true;
            this.Bluetwo = true;
            let dayone = this.DateTwo[index].dayItem[0];
            let daytwo = this.DateTwo[index].dayItem[1]
            if(  dayone<10) {
                dayone = '0' + dayone;
            }else if ( dayone < 10)  {
              daytwo = '0' + daytwo;
            }
            this.frUserConditions.filterday = new Date().getFullYear() + '-' +  dayone + '-' + daytwo;
            console.log( this.frUserConditions.filterday);
            this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
              this.frUserConditions.roomType, this.frUserConditions.devices);
          } else {
            this.DateTwo[index].conditionTwoSelected = false;
            this.Bluetwo =false;
            this.frUserConditions.filterday = new Date();
            this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
              this.frUserConditions.roomType, this.frUserConditions.devices);
          }

        },
        threeConditionClick(index) {
          if (this.frRoomType[index].condition3Selected == false) {
            this.frRoomType.forEach(function (value, index, array) {
              array[index].condition3Selected = false;
            });
            this.frRoomType[index].condition3Selected = true;
            this.Bluethree = true;
            this.frUserConditions.roomType = this.frRoomType[index].typeName;
            this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
              this.frUserConditions.roomType, this.frUserConditions.devices);
          } else {
            this.frRoomType[index].condition3Selected = false;
            this.Bluethree =false;
            this.frUserConditions.roomType = null;
            this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
              this.frUserConditions.roomType, this.frUserConditions.devices);
          }
        },
        itemMutiSelected(index) {
          this.devicesData[index].isitemSelected = !this.devicesData[index].isitemSelected;
          if (!this.devicesData[index].isitemSelected ) {
            this.frUserConditions.devices.pop(this.devicesData[index].personNum);
          }else {
            this.frUserConditions.devices.push(this.devicesData[index].personNum);
          }
          this.Bluefour = false;    //定位该条件是否被选中
          let _this = this;
          this.devicesData.forEach(function (value, index, array) {
            if (array[index].isitemSelected == true) {
              _this.Bluefour = true;
              // _this.frUserConditions.devices.push(array[index].personNum)
            }
          });
          console.log('设备已选中');
          this.sendFilter(this.frUserConditions.minP, this.frUserConditions.maxP, this.frUserConditions.filterday,
            this.frUserConditions.roomType, this.frUserConditions.devices);
        },
        goToDetail (index) {
          // this.$router.push({path: '/detail', query: {id: false}})
          this.CurrentBrowse = this.roomFilterList[index];
          console.log('看看时间');
          console.log(this.roomFilterList[index].room_id);
          this.$router.push({
            name:"detailpages",
            params:{
              date: this.frUserConditions.filterday,
              name:this.roomFilterList[index].name,
              imgSrc:this.roomFilterList[index].imgSrc,
              scan_id: this.roomFilterList[index].room_id,
              max_p:this.roomFilterList[index].min_people
            }
          })
        },
        BackHome () {
          this.$router.go(-1)
        }
      }
    }
</script>

