<template>
  <div>
    <md-toolbar :md-elevation="2">
      <span class="md-title">通知</span>
    </md-toolbar>
    <md-list>
      <md-ripple>
        <md-list-item @click="turnToMessageDetail">
          <md-avatar>
            <img src="../assets/avatar/greet.png" alt="People">
          </md-avatar>
          <span class="md-list-item-text">欢迎使用I Meeting</span>
          <span :class="{ noMeetingNtf: isnoAppN }"><md-badge class="md-primary" :md-content="applicationMsg"   /></span>
        </md-list-item>
      </md-ripple>
      <!--<md-divider class="md-inset"></md-divider>-->

      <md-ripple>
        <md-list-item @click="turnToMeetingMsg">
          <md-avatar>
            <img src="../assets/avatar/emerge.png" alt="People">
          </md-avatar>

          <span class="md-list-item-text">会议提醒</span>
          <span :class="{ noMeetingNtf: isnoMN }"><md-badge  :md-content="meetingStartMsg"    /></span>
        </md-list-item>
      </md-ripple>

      <md-ripple>
        <md-list-item @click="turnToMessageDetail" >
          <md-avatar>
            <img src="../assets/avatar/robot.png" alt="People">
          </md-avatar>
          <span class="md-list-item-text">预约提醒</span>
          <span :class="{ noMeetingNtf: isnoRemind }"><md-badge  :md-content="reservationSuccessMsg"  /></span>
        </md-list-item>
      </md-ripple>

    </md-list>

  </div>
</template>

<script>
    export default {
        name: "messageLayout",
      data: function() {
        return {
          isnoAppN: true,  //系统消息
          isnoMN: true,    //控制开会通知
          isnoRemind: true,  //操作通知
          applicationMsg: this.$store.state.AppMsg,
          meetingStartMsg: this.$store.state.remindMsg,
          reservationSuccessMsg: this.$store.state.OperateMsg
        }
      },
      mounted: function() {
        console.log(this.$store.state.OperateMsg);
        console.log(this.reservationSuccessMsg);
        if (this.applicationMsg > 0) {
          this.isnoAppN = false
        }
        if (this.meetingStartMsg > 0) {
          this.isnoMN = false
        }
        if (this.reservationSuccessMsg > 0) {
          this.isnoRemind = false;
          console.log("消息应该通知")
        }

      },
      methods: {
        turnToMeetingMsg: function () {
          this.$router.push('/meetingMsg')
        },
        turnToMessageDetail: function () {
          this.$router.push('/messagePageDetail')
        }
      }
    }
</script>

<style lang="scss" scoped>
  .md-list {
    width: 100%;
    max-width: 100%;
    display: inline-block;
    vertical-align: top;
  }
  .noMeetingNtf {
    display: none;
  }
</style>



