    <div class="centered-container">

      <md-content class="md-elevation-0 self-full-screen">
        <md-button class="md-icon-button self-location" @click="exitLogin">
<!--
          <md-icon>keyboard_backspace</md-icon>
-->
          <md-icon>clear</md-icon>
        </md-button>
        <div class="title">
          <img src="../assets/imeeting.png">
          <!--<div class="md-body-1">简单、高效、随时随地找到心仪的会议室，高效办公</div>-->
        </div>


        <div class="form">
          <md-field>
            <label>账号/手机号</label>
            <md-input v-model="login.account" autofocus></md-input>
          </md-field>

          <md-field md-has-password>
            <label>密码</label>
            <md-input v-model="login.password" type="password"></md-input>
          </md-field>
        </div>

        <div class="actions md-layout md-alignment-center-space-between">
          <a href="#">忘记密码</a>
          <md-button class="md-raised md-primary" @click="auth">登录</md-button>
        </div>

        <h5 class="md-title self-title-forReg" @click="pushForReg"><span>还没账号？</span>去注册</h5>
        <div class="loading-overlay" v-if="loading">
          <md-progress-spinner md-mode="indeterminate" :md-stroke="2"></md-progress-spinner>
        </div>
      </md-content>
    </div>

  </div>
</template>

<script>
  import axios from 'axios'
  // import qs from 'qs'
    export default {
        name: "lorr",
      data () {
        return {
          loading: false,
          login: {
            account: '',
            password: ''
          },
          initial: null,
          password: 'mysecurepassword'
        }
      },
      methods: {
        getCookies: function() {
          let cookies = document.cookie;
          var list = cookies.split(';');
          console.log(cookies);
          for (var i= 0; i < list.length; i++) {
            var keyname = list[i].split('=');
            if ( keyname[0] == 'Authorization') {

              return decodeURIComponent(keyname[1]);
            }

          }
          return '';
        },
        required () {
          return '19970101101';
        },
        auth () {   //登录验证函数
          this.loading = true;
          let code;
          let _this = this;

          // this.$http.post('/api/user/login',{
            this.$http.post(this.root_url.api_header + '/user/login',{
            loginid:this.login.account,
            password: this.login.password
          },{
            headers: {
              'Content-Type': 'application/json;charset=UTF-8'
            }
          }).then(res=>{
            /**
             *  回调结果开始处理
             */
            let data = res.data;
            // console.log(data);
            code = data.code;
            setTimeout(() => {
              this.loading = false
            }, 1000);
            if ( code == 200) {
              /**
               *  结果是成功开始处理
               */
              this.$store.commit('changeLoginState');     //改变登录状态和获取登录的 Id
              this.$store.commit('setLoginId', data.data.user_id);
              if (data.data.isVerify == true ){
                this.$store.commit('setVerify');
              }
              this.$router.push('/home');     //跳转到个人主页
            }else {
              alert(res.data);
            }

          }).catch(error=>{
            console.log(error);
              this.loading = false
          });

          // this.$router.push('/switch')
        },
        exitLogin: function () {
          this.$router.go(-1)
        },
        pushForReg:function () {
          this.$router.push('/userForReg');
        }
      }
    }

</script>

<style lang="scss" scoped>
  .self-nopadding {
    padding: 0;
  }
  .centered-container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    width: 100%;
  /*height: 100vh;*/
  .self-full-screen {
    /*width: 100%;*/
    max-width: 420px!important;
    height: 100vh!important;
    /*margin-bottom: 20vh;*/
    padding-top: 70px!important;
  }
  .self-location {
    position: absolute;
    left: 10px;
    top: 10px;
  }
    .self-title-forReg {
      /*margin: 8px 8px ;*/
      display: inline-block;

      float: right;
      /*position: absolute;*/
      color: var(--md-theme-default-primary-on-background, #448aff);
      /*right: 10px;*/
      /*top: 12px;*/
      font-size: 14px;
      cursor: pointer;
      &:hover {
        color: var(--md-theme-default-primary-on-background, rgba(68,138,255,0.8));
        text-decoration: underline;
      }
      span {
        color: rgba(0,0,0,0.54);
        font-size: 14px;
        /*opacity: .6;*/
        margin-right: .4rem;
        &:hover {
          text-decoration: none!important;
          color: rgba(0,0,0,0.54);
        }
      }
    }

  .title {
    text-align: center;
    margin-bottom: 30px;
  img {
    margin-bottom: 16px;
    max-width: 80px;
  }
  }
  .actions {
  .md-button {
    margin: 0;
    /*display: inline-block;*/
    /*float: right;*/
  }
  }
  .form {
    margin-bottom: 60px;
  }
  .md-content {
    z-index: 1;
    padding: 40px;
    width: 100%;
    max-width: 400px;
    position: relative;
  }
  .loading-overlay {
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.9);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  }
</style>



