          console.log(this.$route.params.timeStatus);
          let timestamp =  this.$route.params.timeStatus;
          for (var s = 0; s < timestamp.length; s++ ){
            if (timestamp[s] == false) {
              this.timeItems[s].disabledState = false;
            }else {
              this.timeItems[s].disabledState = true;
