          // this.anim.loop = false;
        },

        pause: function () {
          this.anim.pause();
        },

        onSpeedChange: function () {
          this.anim.setSpeed(this.animationSpeed);
        },

        DateFromString:function(date,timestamp) {
          // console.log(123);
          console.log(date);
          var index = false;
          var start;
          var end;
          for (var i = 0;i<timestamp.length;i++)
          {
            if (index == false && timestamp[i].isUsed == true)
            {
              start = i+8;
              index = true;
            }
            if (index == true && timestamp[i].isUsed == false)
            {
              end = i+8;
              index = false;
            }
          }
          if (index==true)
          {
            end = i+8;
          }
          this.startdate = new Date(date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" "+start+":00:00");
          this.enddate = new Date(date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" "+end+":00:00");
          console.log(this.startdate);
          console.log(this.enddate);

        },
