          /*this.timeItems.forEach(function (value, index, array) {
            if (array[index].isUsed == true ) {
              timeSelecterButton = true;

            }
          });*/
          for (let i = 0; i <  this.timeItems.length; i++) {
            if (this.timeItems[i].isUsed == true ) {
              timeSelecterButton = true;
              var first = this.timeItems[i].SelftimeStamps.indexOf("-");
              beginTime = beginTime + this.timeItems[i].SelftimeStamps.substring(0, first);
              console.log(beginTime);
              break;
            }
