              if (this.timeItems[i].isUsed == true ) {
                var two = this.timeItems[i].SelftimeStamps.indexOf("-");
                endTime = endTime + this.timeItems[i].SelftimeStamps.substring(two + 1);
                console.log(endTime);
                break;
              }
            }
            this.vfUserSelectTime = beginTime+ '-' + endTime;   //
          }else {
            this.selectedOrreform = '选择预订时间';
          }
          this.DateFromString( new Date("2019/0"+this.date), this.timeItems);
           console.log(this.startdate);
           console.log(this.enddate);
        },
