            this.timeItems.forEach(function (value, index, array) {
              array[index].isUsed = false;
            });
            // this.clicktwo = 0;
          };

          /!*if(this.clicktwo == 0) {
            this.timeItems[index].isUsed = true;
            this.clicktwo  = 1; //记录已经有选中记录
          }else*!/ if (this.timeItems[index].isUsed == true ) { //如果有记录且按钮已经被选中，检查周围的状态

            if ( index == 0) {    //在开头的情况
              sideright = this.timeItems[index + 1].isUsed
              if (sideright) {
                this.timeItems[index].isUsed = false;
              }else {
                extracted.call(this);
              }
            } else if (index == this.timeItems.length-1) {
              sideleft = this.timeItems[index - 1].isUsed;
              if (sideleft) {
                this.timeItems[index].isUsed = false;
              }else {
                extracted.call(this);
              }
            }else {
              sideright = this.timeItems[index + 1].isUsed;
              sideleft = this.timeItems[index - 1].isUsed;
              if ( sideleft || sideright) {
                this.timeItems[index].isUsed = false;
              }{
                extracted.call(this);
              }
            }

            // this.timeItems[index].isUsed = !this.timeItems[index].isUsed;
            // this.clicktwo --;   //记录置零
          }else if (this.timeItems[index].isUsed == false ){  //如果有记录且点击其他按钮
            if ( index == 0) {    //第二个在开头的情况
              sideright = this.timeItems[index + 1].isUsed
              if (sideright)    {
                this.timeItems[index].isUsed = true;
              }else {
                extracted.call(this);
              }
            } else if (index == this.timeItems.length - 1) {  //第二个在结尾的情况
              sideleft = this.timeItems[index - 1].isUsed
              if (sideleft){
                this.timeItems[index].isUsed = true;
              }
              else {
                extracted.call(this);
                }
            }else {
              sideright = this.timeItems[index + 1].isUsed;
              sideleft = this.timeItems[index - 1].isUsed;
              if ( sideleft || sideright) {
                this.timeItems[index].isUsed = true;
              }else {
                extracted.call(this);
              }
            }

          }*/


          if(this.timeItems[index].isUsed==true)
          {
            if (index==0||index==(this.timeItems.length-1))
            {
              // console.log(222)
              this.timeItems[index].isUsed=false
            }
            else {
              if (this.timeItems[(index-1)].isUsed == true && this.timeItems[(index+1)].isUsed == true)
              {
                for (var i = 0; i < this.timeItems.length; i++) {
                  this.timeItems[i].isUsed = false;
                }
                this.timeItems[index].isUsed=false
