<template>
    <div>

    </div>
</template>

<script>
    export default {
        name: "emptyState",
      beforeRouteEnter(to, from, next) {
        next(vm => {
          vm.$router.replace(from.path)
        })
      }
    }
</script>

<style scoped>

</style>



